/** Flat node with expandable and level information */
interface ExampleFlatNode {
    expandable: boolean;
    name: string;
    level: number;
}


@Component({
    selector: 'multipopup-dialog',
    templateUrl: 'multipopup-dialog.component.html',
})
export class MultipopupDialogComponent {

    constructor(
        public dialogRef: MatDialogRef<MultipopupDialogComponent>,
        @Inject(MAT_DIALOG_DATA) public data: DialogData) {
        this.dataSource.data = TREE_DATA;
    }

    onNoClick(): void {
        this.dialogRef.close();
        //test
    }


    private _transformer = (node: FoodNode, level: number) => {
        return {
            expandable: !!node.children && node.children.length > 0,
            name: node.name,
            popup_code: node.popup_code,
            option_type : node.option_type,
            level: level
        };
    };

    selectedValue: String;


    testNode(node: any){
        this.selectedValue = node.popup_code + '' +node.name;

        console.log(node);
    }



    onKey(event: any) { // without type info
        this.selectedValue = "99-";
        this.selectedValue += event.target.value;
    }

    treeControl = new FlatTreeControl<ExampleFlatNode>(
        node => node.level,
        node => node.expandable
    );

    treeFlattener = new MatTreeFlattener(
        this._transformer,
        node => node.level,
        node => node.expandable,
        node => node.children
    );

    dataSource = new MatTreeFlatDataSource(this.treeControl, this.treeFlattener);

    hasChild = (_: number, node: ExampleFlatNode) => node.expandable;
}
