import {Component, Inject, AfterViewInit} from '@angular/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import { FlatTreeControl } from "@angular/cdk/tree";
import { MatTreeFlatDataSource, MatTreeFlattener} from "@angular/material/tree";

export interface DialogData {
    question: string;
    answer: string;
}

interface FoodNode {
    name: string;
    popup_code?: string;
    option_type?:string;
    popup_level_code?:string;
    children?: FoodNode[];
}


/** Flat node with expandable and level information */
interface ExampleFlatNode {
    expandable: boolean;
    name: string;
    level: number;
}


export interface DialogData {
    animal: string;
    name: string;
    treeData: any;
}

@Component({
    selector: 'dialog-overview-example-dialog',
    templateUrl: 'dialog-overview-example-dialog.html',
})
export class DialogOverviewExampleDialog implements AfterViewInit {

    TREE_DATA: FoodNode[];
    constructor(
        public dialogRef: MatDialogRef<DialogOverviewExampleDialog>,
        @Inject(MAT_DIALOG_DATA) public data: DialogData) {
        console.log(data);
        this.TREE_DATA = [data.treeData]
        this.dataSource.data = this.TREE_DATA;
    }

    onNoClick(): void {
        this.dialogRef.close();
    }

    private _transformer = (node: FoodNode, level: number) => {
        return {
            expandable: !!node.children && node.children.length > 0,
            name: node.name,
            popup_code: node.popup_code,
            popup_level_code: node.popup_level_code,
            option_type : node.option_type,
            level: level
        };
    };

    selectedValue: string;

    testNode(node: any){
        this.selectedValue = node['popup_level_code'] + '-' + node['name'];
        this.data.animal = this.selectedValue;
        console.log(node);
    }

    onKey(event: any, node) { // without type info
        this.selectedValue = node['popup_level_code'] + '-';
        this.selectedValue += event.target.value;
        this.data.animal = this.selectedValue;
    }

    // treeControl = new FlatTreeControl<ExampleFlatNode>(
    //     node => node.level,
    //     node => node.expandable
    // );
    //
    // treeFlattener = new MatTreeFlattener(
    //     this._transformer,
    //     node => node.level,
    //     node => node.expandable,
    //     node => node.children
    // );
    //
    // dataSource = new MatTreeFlatDataSource(this.treeControl, this.treeFlattener);


    treeControl = new FlatTreeControl<ExampleFlatNode>(
        node => node.level, node => node.expandable);

    treeFlattener = new MatTreeFlattener(
        this._transformer, node => node.level, node => node.expandable, node => node.children);

    dataSource = new MatTreeFlatDataSource(this.treeControl, this.treeFlattener);

    hasChild = (_: number, node: ExampleFlatNode) => node.expandable;


    ngAfterViewInit() {
        for (let i = 0; i < this.treeControl.dataNodes.length; i++) {
            if(this.treeControl.dataNodes[i]['option_type'] == 'Question'){
                this.treeControl.expand(this.treeControl.dataNodes[i]);
            }
            console.log(this.treeControl.dataNodes[i]);
        }
    }

}
