import {Component, OnDestroy, OnInit, ViewChild, ViewEncapsulation} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {ActivatedRoute, Router} from '@angular/router';
import {Subject} from "rxjs";
import { environment } from './../../../../environments/environment';
import { DomSanitizer } from '@angular/platform-browser';
import { image } from './image.const';
import {FuseNavigationService} from '@fuse/components/navigation/navigation.service';
import {MatSnackBar} from "@angular/material/snack-bar";
import { TranslateService } from '@ngx-translate/core';
import { FuseTranslationLoaderService } from '@fuse/services/translation-loader.service';
import { locale as english } from 'app/main/i18n/en';
import { locale as spanish } from 'app/main/i18n/es';
import { locale as french } from 'app/main/i18n/fr';
import { locale as hungarian } from 'app/main/i18n/hu';
import { locale as dutch } from 'app/main/i18n/nl';
import { locale as portuguese } from 'app/main/i18n/pt';
import { locale as chinese } from 'app/main/i18n/zh';
import {Title} from "@angular/platform-browser";
import { ConfigLoaderService } from '../../../config-loader.service';

@Component({
    selector     : 'apps-qwapps-home',
    templateUrl  : './qwapp-alarms.component.html',
    styleUrls    : ['./qwapp-alarms.component.scss'],
})
export class QwAppAlarmsComponent implements OnInit, OnDestroy
{
    file: any;
    title: string;
    userFileAccess: string;
    rows: any[];
    loadingIndicator: boolean;
    reorderable: boolean;
    private _unsubscribeAll: Subject<any>;
    selected = [];
    apiUrl: string;
    imageSource;
    selectedVariable;
    ackChecked = false;
    ackComments = '';
    chartLoader: boolean;
    formLoader: boolean;
    tabularLoader: boolean;

    constructor(
        private http: HttpClient,
        public router: Router,
        private route: ActivatedRoute,
        private sanitizer: DomSanitizer,
        private _fuseNavigationService: FuseNavigationService,
        private _snackBar: MatSnackBar,
        private _translateService: TranslateService,
        private _fuseTranslationLoaderService: FuseTranslationLoaderService,
        private titleService:Title,
        private configLoaderService: ConfigLoaderService
    )
    {
        this.titleService.setTitle(this._translateService.instant('QWSUM.5001'));
        this.file = decodeURI(this.route.snapshot.queryParamMap.get('file'));
        this.title = decodeURI(this.route.snapshot.queryParamMap.get('title'));
        this.userFileAccess = decodeURI(this.route.snapshot.queryParamMap.get('access'));

        // this.apiUrl = environment.apiUrl;
        this.apiUrl = this.configLoaderService.apiUrl;
        this.loadingIndicator = true;
        this.reorderable = true;
        // Set the private defaults
        this._unsubscribeAll = new Subject();

    }

    // -----------------------------------------------------------------------------------------------------
    // @ Lifecycle hooks
    // -----------------------------------------------------------------------------------------------------

    /**
     * On init
     */

    chartVaiables;
    ngOnInit(): void
    {
        const token = localStorage.getItem('_token');

        this.http.post<any>(this.apiUrl + 'api/QWFilesList',
            {token : token}).subscribe(data => {
            this.loadingIndicator = false;

            if (data['status'] === 'error') {
                alert(data['message']);
                this.router.navigate(['/']);

            } else if (data['status'] === 'success') {
                this.rows = data['files'];
            }
        });
        this.alarmChartData();
        this.addNavItemWithCustomFunction();
    }

    ngOnDestroy(): void
    {
        this._fuseNavigationService.removeNavigationItem('applications');
        this._fuseNavigationService.removeNavigationItem('logsheet');
        this._fuseNavigationService.removeNavigationItem('alerts');

        // Unsubscribe from all subscriptions
        this._unsubscribeAll.next();
        this._unsubscribeAll.complete();
    }
    // tslint:disable-next-line:typedef
    addNavItemWithCustomFunction() {

        const newNavItem4 = {
            id       : 'applications',
            title    : this._translateService.instant('NAV.APPLICATIONS'),
            url      : '/apps/qwapps/list',
            translate: 'NAV.APPLICATIONS',
            type     : 'group',
            icon     : 'home',
            // classes: 'accent',
        };

        const newNavItem1 = {
            id: 'logsheet',
            title: this._translateService.instant('NAV.LOG_SHEET'),
            type: 'group',
            icon: 'view_module',
            translate: 'NAV.LOG_SHEET',
            function: () => {
                this.openLogSheet();
            }
        };

        const newNavItem2 = {
            id: 'alerts',
            title: this._translateService.instant('NAV.ALERTS'),
            type: 'group',
            icon: 'notifications_active',
            translate: 'NAV.ALERTS',
            classes: 'accent'
        };

        this._fuseNavigationService.addNavigationItem(newNavItem4, 'end');
        this._fuseNavigationService.addNavigationItem(newNavItem1, 'end');
        this._fuseNavigationService.addNavigationItem(newNavItem2, 'end');
    }
    selectedVariableEvent(item){
        this.selectedVariable =  item;
        this.selectedVariableAlarmAuditLogs = [];
        this.getVariableAlarmAuditLogs();
    }

    openLogSheet(){
        this.router.navigate(['/apps/qwapps/info'],
            {
                queryParams:
                    {
                        title: this.title,
                        file: this.file,
                        access: this.userFileAccess
                    }
            });
    }


    alarmChartData() {
        this.chartLoader = true;
        const token = localStorage.getItem('_token');
        this.http.post<any>(this.apiUrl + 'api/QWAlarmsData',
            {token: token, file: this.file}).subscribe(data => {
            this.chartLoader = false;
            if (data['status'] === 'error') {
                this._snackBar.open(data['message'], '', {duration: 3000});
            } else if (data['status'] === 'success') {
                this.chartVaiables = data['filedata'];
                if(this.chartVaiables[0]){
                    this.selectedVariableEvent(this.chartVaiables[0])
                }
                //this.selectedVariable = this.chartVaiables[0];

                this._snackBar.open(this.title + ' ' + this._translateService.instant('QWSUM.5041'), '', {duration: 3000});
            }else{
                this._snackBar.open(this.title + '' + this._translateService.instant('QWADD.4042'), '', {duration: 3000});
            }
        });
    }

    selectedVariableAlarmAuditLogs = [];
    getVariableAlarmAuditLogs(){
        let variabe_number = this.selectedVariable['variableNumber'];
        this.tabularLoader = true;
        const token = localStorage.getItem('_token');
        this.http.post<any>(this.apiUrl + 'api/QWGetAlarmsAuditLog',
            {token: token, file: this.file, variabe_number: variabe_number}).subscribe(data => {
            this.tabularLoader = false;
            if (data['status'] === 'error') {
                //TODO
            } else if (data['status'] === 'success') {
                this.selectedVariableAlarmAuditLogs = data['filedata'];
            }
        });
    }

    submitVariableAlarmAuditLog(){
        this.formLoader = true;
        let variabe_number = this.selectedVariable['variableNumber'];
        let comment = this.ackComments.length > 0 ? this.ackComments : 'Acknowledged.';
        const token = localStorage.getItem('_token');
        this.http.post<any>(this.apiUrl + 'api/QWSaveAlarmsAuditLog',

            {token: token, file: this.file, variabe_number: variabe_number, comment: comment}).subscribe(data => {
            this.formLoader = false;
            if (data['status'] === 'error') {
                this._snackBar.open(data['message'], '', {duration: 9000});
            } else if (data['status'] === 'success') {
                this.ackComments = '';
                this.ackChecked = false;
                this._snackBar.open(this._translateService.instant('QWADD.4041'), '', {duration: 3000});
                this.getVariableAlarmAuditLogs();
            }
        });
    }
}



