    // tslint:disable-next-line:typedef
    getQWVariables() {
        const token = localStorage.getItem('_token');
        this.http.post<any>(this.apiUrl + 'api/QWVariables',
            {token: token, file: this.file}).subscribe(data => {
            if (data['status'] === 'error') {
                alert(data['message']);
            } else if (data['status'] === 'success') {
                this.isVariablesLoaded = true;
                this.variablesData = data['filedata'];
            }
        });
    }

    getLastRecord() {
        const token = localStorage.getItem('_token');
        this.http.post<any>(this.apiUrl + 'api/QWGetLastRecord',
            {token: token, file: this.file}).subscribe(data => {
            if (data['status'] === 'error') {
                alert(data['message']);
            } else if (data['status'] === 'success') {
                this.addLastRecord = data['record'];
            }
        });
    }

    // tslint:disable-next-line:typedef
    noRecordsFlag:boolean = false;
    logData() {
        this.noRecordsFlag = false;
        this.loadingIndicator = true;
        this.isFileLogDataLoaded = false;
        const token = localStorage.getItem('_token');
        this.http.post<any>(this.apiUrl + 'api/QWLogData',
            {token: token, file: this.file}).subscribe(data => {
            this.loadingIndicator = false;
            if (data['status'] === 'error') {
                if(data['message']=='No Records found'){
                    this.noRecordsFlag = true;
                    this._snackBar.open(data['message'], '', {duration: 3000});
                }else{
                    this._snackBar.open(data['message'], '', {duration: 3000});
                    this.router.navigate(['/']);
                }

            } else if (data['status'] === 'success') {


                this.isFileLogDataLoaded = true;
                this._snackBar.open(this.title + ' '+ this._translateService.instant('QWLOG.3021'), '', {duration: 3000});
                this.fileLogData = data['filedata'];
                this.RowLevel1 = data['RowLevel1'];
                this.RowLevel2 = data['RowLevel2'];
                this.RowLevel3 = data['RowLevel3'];
                this.RowLevel4 = data['RowLevel4'];
                this.RowLevel5 = data['RowLevel5'];

                const arr = data['filedata'];
                this.columns = data['columns'];
                this.columnslist = data['columnslist'];
                this.lastRow = arr[arr.length - 1];

                console.log("this.lastRow");
                console.log(this.lastRow);

                setTimeout(() => {
                    this.scrollBottom();
                }, 2000);
            }
        });
    }

    // tslint:disable-next-line:typedef
    getColor(record, column) {
        const cssVariable = column + '_css_color_class';
        return record[cssVariable];
    }

    // tslint:disable-next-line:typedef
    getClassNumeric(record, column) {
        const varName = column + '_field_type';
        const varValue = record[varName];

        if (varValue !== undefined && varValue != null) {
            if (varValue.localeCompare('qwNumeric') === 0 || varValue.localeCompare('qwCalculated') === 0 ||
                varValue.localeCompare('qwAverage') === 0 || varValue.localeCompare('qwRange') === 0) {
                return true;
            } else {
                return false;
            }
        } else {
            return false;
        }
    }

